*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



h1, h2, h3, p {
  color: rgb(219, 219, 219);
}

.heroBox p {
  display: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  width: 100%;
  background-color: #1f1e23;
}

.heroBox{
  display: flex;
  height: 800px;
  background-color: #1f1e23;
  width: 100%;
  justify-content: space-between;
  
}

.culture {
  height: 100%;
  z-index: 2;
  overflow-x: hidden;
}

.heroBox h1 {
  color: rgb(219, 219, 219);
  -ms-grid-row-align: center;
  align-self: center;
  width: 30%;
  margin: 0 auto;
  font-size: 60px;
  -ms-grid-column-align: bottom;
      justify-self: bottom;
  font-family: 'Fraunces', serif;
}

.first {
  overflow-x: hidden;
  border-bottom: 1px solid rgb(219, 219, 219);
}


.player-wrapper {
  position: relative;
  padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
  overflow: hidden;
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.welcome p {
  z-index: 3;
  width: 30%;
}

.overlay p {
  position:absolute;
  top:30%;
  left:5%;
  z-index:1;
  padding: 20px;
  background-color: #1f1e23;
  height: 50vh;
}

.overlay {
  position: relative;
  bottom: 450px;
  display: none;
}

.inCards {
  display: flex;
  flex-direction: row;
  width: 80%;
  max-width: 2400px;
  margin: 10% auto;
  justify-content: space-around;
}

.left {
  -ms-grid-column-align: end;
      justify-self: end;
  width: 45%;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
  height: auto;
}

.right {
  -ms-grid-column-align: start;
      justify-self: start;
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.titlebar {
  margin: 0 auto;
  background-color: #1f1e23;
  color: rgb(219, 219, 219);
  padding: 30px;
}

.titlebar h2 {
  margin-left: 50px
}

.inCards span {
  font-size: 60px;
  width: 60%;
  padding-bottom: 50px;
  border-bottom:1px solid rgb(219, 219, 219);
}

.c {
  display: flex;
  justify-content: center;
  align-items: center;
}

.c ul {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


.biglist {
  font-family: "Cosi Azure";
  font-size: 60px;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.3);
}

span {
  position: relative;
  display: inline-block;
}

.titlebar span {
 color: #0cae00;
}

.navigation-item span:before {
  width: 0;
  color: #ffffff;
  overflow: hidden;
  position: absolute;
  content: attr(data-text);
  transition: all 1s cubic-bezier(0.84, 0, 0.08, 0.99);
}

.navigation-item span:hover:before {
  width: 100%;
}


.side {
  display: flex;
  flex-direction: column;
  opacity: 0;
  position: relative;
  right: 100px;
  z-index: -20;
  margin-left: 20px;
}

.side img {
  height: 40vh;
  object-fit: cover;
  object-position: center;
}

.side span {
  padding: 10px;
}

.navigation-item {
  display: flex;
  flex-direction: row;
  margin-bottom: 50px;
  margin-top: 50px;
  width: 100%;
}

.horizontal {
  height: 100vh;
  display: flex;
  left: 0;
  background: firebrick;
  overflow: hidden;
}

.pin-wrap {
  height: 100vh;
  display: flex;
  padding: 50px 10vw;
  justify-content: flex-start;
  align-items: center;

}

.infoblock {
  height: 100vh;
  width: 100vw;
  min-width: 100vw;
  display: flex;
  flex-direction: row;
  padding-right: 100px;
  justify-content: space-around;
}

.infoblock img {
  width: 30%;
  max-width: 800px;
  height: auto;
  justify-content: end;
  object-fit: cover;
  object-position: center;
}

.infoblock-text {
  color: black;
display: flex;
flex-direction: column;
-ms-grid-row-align: center;
align-self: center;
justify-content: center;
width: 30%
}

.headspan {
  font-size: 60px;
  display:flex;
  flex-direction: column;
}

.headspan2 {
  position: relative;
  left: 10%;
}

