    object-fit: cover;
    object-position: center;
    height: 100%;
  }
  
  .infoblock-text {
    height: 70%;
    width: 60vw;
   
  }

  .infoblock  {
   width: 150vw;
   
  }

  .infoblock h2 {
    display: flex;
    flex-wrap: wrap;
  }

  .side {
    width: 50%;
  }

  .biglist {
    font-size: 40px;
  }


  .headspan {
    width: 20vw
  }

  .headspan2 {
    position: relative;
    left: 0;
  }

  .welcome p {
    width: 40%;
    height: 60vh;
  }
   
}


